import React, { useEffect, useState } from "react";
import PropTypes from "prop-types"; // PropType 
import "./CommonLayout.css";
import Header from "../header/header";
import axiosInstance from "../../../api/AxiosInstance";

const CommonLayout = ({
  dashboardComponent,
  dailyComponent,
  weeklyComponent,
  longitude,
  latitude,
}) => {
  const [weatherNow, setWeatherNow] = useState(null); //   
  const [locationError, setLocationError] = useState(null); //   
  const [backgroundImage, setBackgroundImage] = useState(""); //   

  useEffect(() => {
    const fetchWeatherNow = async () => {
      //   

      try {
        //     API 
        const weatherNowResponse = await axiosInstance.get(
          `/api/weatherNow?longitude=${longitude}&latitude=${latitude}`
        );
        setWeatherNow(weatherNowResponse.data); //    

        //      
        const backgroundImages = {
          0: "lightning.jpg",
          1: "rainy.jpg",
          2: "rainy.jpg",
          3: "snowy.jpg",
          4: "mist.jpg",
          5: "windy.jpg",
          6: "windy.jpg",
          7: "sunny.jpg",
          8: "littleCloudy.jpg",
          9: "cloudy.jpg",
          10: "rainy.jpg",
        };

        const selectedImage =
          backgroundImages[weatherNowResponse.data.weather] || "sunny.jpg";
        setBackgroundImage(`/assets/common/img/${selectedImage}`); //   
      } catch (error) {
        console.error("Failed to fetch weatherNow data:", error);
        setWeatherNow(null); //    null 
      }
    };
    fetchWeatherNow(); //  
  }, []);

  return (
    <div
      className="WeatherLayout"
      style={{
        backgroundImage: `url(${backgroundImage})`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        height: "100vh",
      }}
    >
      <Header></Header>
      <div className="WeatherContainer">
        {/*   */}
        <section>{dashboardComponent}</section>

        {/*    */}
        <section>{dailyComponent}</section>

        {/*    */}
        <section>{weeklyComponent}</section>
      </div>
    </div>
  );
};

// PropTypes   prop 
CommonLayout.propTypes = {
  dashboardComponent: PropTypes.node.isRequired, //  
  dailyComponent: PropTypes.node.isRequired, //   
  weeklyComponent: PropTypes.node.isRequired, //   
  longitude: PropTypes.number.isRequired, // 
  latitude: PropTypes.number.isRequired, // 
};



export default CommonLayout;
