import React from "react";
import PropTypes from "prop-types"; // PropTypes  Prop  

const CommonDashboard = ({
  data, //  
  forecastNow, //   
  showMainIcon = true, // mainIcon  
  showTempNow = true, //    
  baseMainIconPath, //    
  customContent, //   ()
  children, //   
  iconName,
}) => {
  return (
    <div className="dashboard">
      {/*   */}
      <span className="level2Address">
        <img className="gpsIcon" src={`/assets/common/icon/gpsIcon.svg`} alt="gps " />
        {data.address.level2}
      </span>
      <span className="level1Address">{data.address.level1}</span>

      {/*      */}
      {showMainIcon || showTempNow ? (
        <div className="mainIconContainer">
          {showMainIcon && (
            <img
              className="mainIcon"
              src={`${baseMainIconPath}${iconName}.svg`}
              alt=" "
            />
          )}
          {showTempNow && (
            <div className="tempNow">{forecastNow}</div>
          )}
        </div>
      ) : null}

      {/*   -  */}
      <div className="custom-content">{customContent}</div>

      {/*     */}
      {children}
    </div>
  );
};

// PropTypes 
CommonDashboard.propTypes = {
  data: PropTypes.shape({
    address: PropTypes.shape({
      level1: PropTypes.string.isRequired,
      level2: PropTypes.string.isRequired,
    }).isRequired,
    temp: PropTypes.number.isRequired,
  }).isRequired, // data 
  forecastNow: PropTypes.shape({
    weather: PropTypes.string.isRequired, //  weather 
  }),
  showMainIcon: PropTypes.bool,
  showTempNow: PropTypes.bool,
  mainIconPath: PropTypes.string, //      
  children: PropTypes.node.isRequired,
  iconName: PropTypes.string.isRequired,
  baseMainIconPath: PropTypes.string.isRequired,
};

export default CommonDashboard;
