import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import axiosInstance from "../../api/AxiosInstance";
import CustomSlider from "./CustomSlider";

const FeedbackModal = ({ onClose, onSuccess }) => {
  const [scores, setScores] = useState({
    morning: 0,
    noon: 0,
    evening: 0
  });
  const [comment, setComment] = useState("");
  const navigate = useNavigate();

  const handleSliderChange = (time, value) => {
    setScores((prev) => ({
      ...prev,
      [time]: value
    }));
  };

  const handleSubmit = async () => {
    try {
      await axiosInstance.post("/api/clothing", {
        morningScore: scores.morning,
        noonScore: scores.noon,
        eveningScore: scores.evening,
        comment: comment
      });
      alert("  .");

      //   onSuccess  
      if (onSuccess) {
        onSuccess();
      }

      navigate("/clothing");
    } catch (error) {
      console.error(error);
      alert("  .");
      onClose(); //     
      navigate("/clothing");
    }
  };

  return (
    <div className="modalBackdrop">
      <div className="modalBox">
        <span className="modalTitle">  ?</span>

        {["morning", "noon", "evening"].map((timeKey, i) => {
          const timeLabel = ["", "", ""][i];
          return (
            <div key={timeKey} className="sliderRow">
              <div className="iconLabel">
                <img src={`/assets/clothing/icon/${timeLabel}.svg`}
                     alt={timeLabel} />
                <span>{timeLabel}</span>
              </div>
              <CustomSlider
                value={scores[timeKey]}
                onChange={(val) => handleSliderChange(timeKey, val)}
              />
              <div className="sliderLabels">
                <span className="cold"> </span>
                <span className="okay"></span>
                <span className="hot"> </span>
              </div>
            </div>
          );
        })}

        <div className="feedbackString">
          <label htmlFor="feedback">    ?</label>
          <textarea
            id="feedback"
            placeholder="  AI    !"
            rows="3"
            className="feedbackTextarea"
            value={comment}
            onChange={(e) => setComment(e.target.value)}
          />
        </div>

        <div className="modalButtons">
          <button onClick={handleSubmit}></button>
          <button onClick={onClose}></button>
        </div>
      </div>
    </div>
  );
};

export default FeedbackModal;