import React from "react";
import PropTypes from "prop-types";

const CommonWeeklyForecast = ({
  data,
  headerName,
  iconPosition,
  iconPath,
  isTempMode,
  isIconMode = false,
}) => {
  return (
    <div className="commonWeeklyForecast">
      <div className="weeklyHeader">
        <img alt={" "} src="/assets/common/icon/calendar.svg"/>
        <span>{headerName}</span>
      </div>
      {data.map((day, index) => {
        let underBarDiv;
        if (isTempMode) {
          underBarDiv = (
            <div className="minMaxTempsDiv">
              <div className="minTemp">{Math.round(day.minTemp)}</div>
              <div className="maxTemp">{Math.round(day.maxTemp)}</div>
            </div>
          );
        } else if (!isIconMode) {
          underBarDiv = (
            <div className="morningNoonEveningDiv">
              <div className="morning"></div>
              <div className="noon"></div>
              <div className="evening"></div>
            </div>
          );}
      

        return (
          <div key={index} className="weeklyItem">
            <div className="dayOfWeek">
              {index === 0 ? "" : day.dayOfWeek[0]}
            </div>
            {/*   */}
            {(iconPosition === "left" || iconPosition === "both") && (
              <span className="leftIcon">
                <img
                  className="leftIconImage"
                  src={`${iconPath}/${day.leftIcon}.svg`}
                  alt=" "
                />
                {isIconMode && <div className="leftIcon">{day.leftIconLabelText}</div>}
              </span>
            )}
            {/*   */}
            <div className="temperatureVisualsContainer">
              {isTempMode && (
                <div
                  className="temperatureBar"
                  style={{
                    backgroundImage: getTemperatureGradient(
                      day.minTemp,
                      day.maxTemp,
                    ),
                  }}
                ></div>
              )}
              {!isIconMode && underBarDiv}
            </div>
            {iconPosition === "both" && (
              <span className="rightIcon">
                <img
                  className="rightIconImage"
                  src={`${iconPath}/${day.rightIcon}.svg`}
                  alt=" "
                />
                {isIconMode && <div className="rightIconText">{day.rightIconLabelText}</div>}
                
              </span>
    
            )}
          </div>
        );
      })}
    </div>
  );
};

// PropTypes 
CommonWeeklyForecast.propTypes = {
  data: PropTypes.arrayOf(
    PropTypes.shape({
      dayOfWeek: PropTypes.string.isRequired, // 
      leftIcon: PropTypes.string,
      rightIcon: PropTypes.string,
      minTemp: PropTypes.string,
      maxTemp: PropTypes.string,
    }),
  ).isRequired, //   

  headerName: PropTypes.string.isRequired,
  iconPath: PropTypes.string.isRequired,
  tempLabels: PropTypes.shape({
    min: PropTypes.string, //   
    max: PropTypes.string, //   
  }),
  iconPosition: PropTypes.oneOf(["left", "both", "nothing"]).isRequired,
  isTempMode: PropTypes.bool.isRequired,
};

const getTemperatureGradient = (...temperatures) => {
  const getColorForTemp = (temp) => {
    if (temp < -15) {
      return "#191970"; // -15C : Midnight Blue
    }
    if (temp < -10) {
      return "#000080"; // -10C : Navy
    }
    if (temp < -5) {
      return "#00008B"; // -5C : Dark Blue
    }
    if (temp < 0) {
      return "#0051ff"; // 0C : Blue
    }
    if (temp < 5) {
      return "#1eecff"; // 5C : Dodger Blue
    }
    if (temp < 10) {
      return "#55ff00"; // 10C : Deep Sky Blue
    }
    if (temp < 15) {
      return "#beff00"; // 15C : Cyan
    }
    if (temp < 18) {
      return "#ebff00"; // 18C : Aquamarine
    }
    if (temp < 20) {
      return "#fff900"; // 20C : Light Green
    }
    if (temp < 23) {
      return "#ffb700"; // 23C : Green Yellow
    }
    if (temp < 25) {
      return "#ff8f00"; // 25C : Yellow
    }
    if (temp < 27) {
      return "#ff6d00"; // 27C : Gold
    }
    if (temp < 30) {
      return "#ff3900"; // 30C : Orange
    }
    if (temp < 33) {
      return "#ff5d18"; // 33C : Dark Orange
    }
    if (temp < 35) {
      return "#ff2800"; // 35C : Tomato
    }
    if (temp < 40) {
      return "#a60000"; // 40C : Orange Red
    }
    return "#640000";
  };

  //      
  const colors = temperatures.map(getColorForTemp);

  //     
  return `linear-gradient(to right, ${colors.join(", ")})`;
};

export default CommonWeeklyForecast;
